/*! 关联账号 css */
.linked-account {
  padding: 0;
  min-height: 100%;
  background-color: #fff;
  .info-hint {
    background-color: #fff;
    height: @120px;
    padding: @30px @30px 0;
    > div {
      height: @60px;
    }
    .left {
      width: @200px;
      height: 100%;
      background-color: #027df7;
      border-top-left-radius: @4px;
      border-bottom-left-radius: @4px;
      color: #fff;
      font-size: @30px;
      font-weight: 600;
    }
    .icon-horn {
      width: @32px;
      height: @28px;
      background-image: url(../images/linked-account/icon-horn.png);
      margin-right: @10px;
    }
    .right {
      height: 100%;
      background-color: #e9f4ff;
      color: #666;
      font-size: @24px;
      text-align: center;
      border-top-right-radius: @4px;
      border-bottom-right-radius: @4px;
      span {
        display: inline-block;
        color: #027df7;
        font-size: @30px;
        font-weight: 500;
        padding: 0 @10px;
      }
    }
  }
  .wrapper {
    background-color: #fff;
    border-top: @20px solid #f7f8f9;
    &.last {
      li:last-of-type {
        border-bottom: 1px solid #ddd;
      }
      li {
        img {
          width: @76px;
          height: @76px;
          margin-left: @12px;
          margin-right: @36px;
        }
      }
    }
    ul {
      padding: 0 @30px;
    }
    li {
      border-bottom: 1px solid #ddd;
      height: @120px;
      &:last-of-type {
        border: 0;
      }
      img {
        width: @108px;
        height: @80px;
        margin-right: @16px;
      }
      p {
        font-size: @28px;
        line-height: 1;
        color: #333;
      }
      span {
        display: inline-block;
        font-size: @24px;
        color: #999;
        line-height: 1;
        padding-top: @14px;
      }
      .btn {
        width: @140px;
        height: @44px;
        line-height: @44px;
        background-color: transparent;
        color: #007cf7;
        font-size: @24px;
        border: 1px solid #007cf7;
      }
    }
  }
  .btn-box {
    padding: @40px @60px;
  }
}
